html,
body {
  color: #dddddd;
  width: 100%;
  height: 100%;
  font-family: "Bebas Neue";
  font-size: 20px;
  overflow: hidden;
  background-image: linear-gradient(#0e0d0d 33%, #131212 67%); }

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  -webkit-backface-visibility: hidden; }

.fill {
  width: 100%;
  height: 100%; }

.hide {
  display: none; }

.page .content {
  position: relative; }
